<template>
  <div>
    <div class="home">
      <p>ici le home</p>
    </div>
    <vue-p5
            @setup="setup"
            @draw="draw">
    </vue-p5>

    <v-btn :color="this.running ? 'green' : 'orange'" dark @click="pause">

    </v-btn>
  </div>
</template>

<script>
    import VueP5 from 'vue-p5'
    // @ is an alias to /src

    export default {
        name: 'Home',
        components: {
            "vue-p5": VueP5
        },
        data: () => ({
          running: true
        }),
        methods: {
            setup (sketch) {
              sketch.createCanvas(800, 600)
            },
            draw (sketch) {
                sketch.background(0);
                sketch.fill(255);
                sketch.rect(100, 100, 100, 100)
            },
            pause () {
                this.running = !this.running;
            }
        }
    }
</script>
